@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400|Lora");
body {
  background: #f3f3f3;
}

/* div box styling */
.container {
  width: 650px;
  height: 50px;
}

.homePanel {
  background: #ffff;
  width: 650px;
  height: 480px;
  position: absolute;
  top: 25%;
  border-radius: 5px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
}

.rescuePanel {
  position: absolute;
  top: -10%;
  left: 5%;
  background: #ffc436;
  width: 320px;
  height: 550px;
  border-radius: 5px;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
  transition: all 0.5s ease-in-out;
  z-index: 2;
}

.nodisplay {
  display: none;
  transition: all 0.5s ease;
}

.qrPanel {
  position: absolute;
  width: 50%;
  transition: 1s all ease;
  right: -2%;
}

/* font & button styling */
h1 {
  font-family: "Open Sans", sans-serif;
  text-align: center;
  margin-top: 95px;
  text-transform: uppercase;
  color: #f6f6f6;
  font-size: 2em;
  letter-spacing: 8px;
}

span {
  color: #ffc436;
}

.howTo {
  background: #dbe2e9;
  color: #333;
  position: fixed;
  top: 0;
  right: 0;
  width: 500px;
  height: 100%;
  -webkit-transition-duration: 0.3s;
  -moz-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
}
